/* remove preset class generated */
.gorgias-loaded {
  margin: 0 !important;
}
.contacted {
  text-decoration: line-through;
}
#nav {
  text-align: center;
  padding: 20px;
}
#nav li {
  float: left;
  padding: 0 20px;
  font-family: "Roboto Mono";
  text-transform: uppercase;
  color: #000;
  list-style-type: none;
}
#nav li:hover {
  cursor: pointer;
}
