}
#add-user {
  text-align: center;
}
#add-user #submit-user {
  padding: 13px 30px;
  margin: 15px 0;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: 1px solid #000;
  border-radius: 5px;
  color: #000;
  text-transform: uppercase;
  /* color: #fff; */
  background-color: transparent;
}
#submit-user:hover {
  cursor: pointer;
  -webkit-box-shadow: 3px 3px rgb(104, 104, 104);
          box-shadow: 3px 3px rgb(104, 104, 104);
}
